import PageButton from '../../components/button.vue';

export default {
	components: {

		PageButton
	},
	data() {
		return {
			temp: {
				bankName: '',
				bankAccount: ''
			},
			array: [ '工商银行','招商银行', '农业银行', '建设银行','中国银行'],
			index: null,
		}
	},
	methods: {
		bindPickerChange: function(e) {
			this.index = e.target.value
			this.temp.bankName = this.array[this.index]
		},
		submit: function() {
			let that = this;
			if (!that.$util.isEmpty(that.temp.bankName)) {
				uni.showToast({
					title: '请选择开户行',
					icon: 'none',
					duration: 2000
				});
				return;
			}
			if (!that.$util.isEmpty(that.temp.bankAccount)) {
				uni.showToast({
					title: '请输入银行卡号',
					icon: 'none',
					duration: 2000
				});
				return;
			}

			that.$util.request({
				url: "/didi-doctor/doctorbankinfo/add",
				param: that.temp,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					uni.showToast({
						title: '新增成功',
						icon: 'success',
						duration: 2000
					})
					setTimeout(function() {
						uni.navigateBack({
							delta: 1
						})
					}, 500)
				},
				error: function() {}
			})
		}
	}
}
